import './Main.css';
import React, { useState,useEffect } from 'react';

const API = 'https://api.scryfall.com/cards/search?q=lang:ja+name='

function Main(){
  const[searchWord, setSearchWord] = useState('');
  const[cardList, setCardList] = useState([]);

  useEffect(() => {
    const timeoutId = setTimeout(() => {
      getCardList(searchWord)
      .then(cardList => {
        cardList ? setCardList(cardList.map(cardInfo => ShowCardInfo(cardInfo))) : setCardList([])
      })
    }, 500);
    return () => {
      clearTimeout(timeoutId)
    }
  },[searchWord])

  function handleChange(e){
    setSearchWord(e.target.value)
  }

  return (
    <div>
      <div>カード名　<input type="search" value={searchWord} onChange={handleChange} /></div>
      <div>
      {cardList}
      </div>
    </div>
  );
}

function getCardList(searchWord){
  return new Promise(function(resolve){
    fetch(API + searchWord)
    .then(res => res.json())
    .then(data => {resolve(data.data)})
  })
}

function ShowCardInfo(cardInfo){
  const image = cardInfo.image_uris ? cardInfo.image_uris : {}
  return(
    <div id="cardContainer">
      <div id="printed_name">日本語名：{cardInfo.printed_name}</div>
      <div id ="name">英語名　：{cardInfo.name}</div>
      <div id="color_identity">固有色　：{cardInfo.color_identity}</div>
      <div id="printed_text">テキスト：<br />{cardInfo.printed_text}</div>
      <div id="image"><img src={image.small}></img></div>
    </div>
  );
}

export default Main;
